  height: 2.143rem;
  font-size: 1.143rem;
}
.Login_subtitle__1L4xT {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.143rem;
  font-weight: 100;
  padding-bottom: 0.357rem;
  color: #505455;
}
.Login_subtitle1__E7dEN,
.Login_subtitle2__EgZZj {
