}
/* 모바일 세로 */
@media (max-width: 767.98px) {
  .Login_root__18z7Z {
    display: flex;
    justify-content: center;
  }
  .Login_app__2T_Xb {
    background-color: white;
    padding: 2.3rem 0;
    height: 0vh;
  }
  .Login_logo__1TqGR {
    padding-bottom: 22vh;
  }
  .Login_bible__1Qp0a {
    padding-bottom: 18vh;
    font-family: "HallymGothic-Regular";
  }
  .Login_bibleVerse__1Nuni {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.3rem;
    font-weight: 600;
    color: #505455;
  }
  .Login_bibleVerse1over2__1zsam,
  .Login_bibleVerse2over2__DjOLp {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .Login_bibleVerse1__qyx6K,
  .Login_bibleVerse2__2jsMb,
  .Login_bibleVerse3__1I4gs {
    display: block;
    padding-bottom: 0.714rem;
  }
  .Login_bibleVerse4__1tJp7 {
    padding-bottom: 0.929rem;
  }
  .Login_bibleName__1iQ7p {
